@import url('https://fonts.googleapis.com/css2?family=Freehand&display=swap');

a{
  color: black !important;
}

.brand-logo,h2{
  font-family: 'Freehand', cursive;
}

.mycard{
  margin-top: 30px;
}

.input-field input[type=text]:focus {
  border-bottom: 1px solid #617edb !important;
  box-shadow: 0 1px 0 0 #617edb !important;
}
.input-field input[type=password]:focus {
  border-bottom: 1px solid #617edb !important;
  box-shadow: 0 1px 0 0 #617edb !important;
}

.auth-card{
  padding: 20px;
  text-align: center;
  max-width: 400px;
  margin: 10px auto;
}

.gallery{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.item{
  width:30%;
}

.home-card{
  max-width: 500px;
  height: max-content;
  margin: 26px auto;
}

.material-icons:hover{
  cursor: pointer;
}